.select{
    display: flex;
    width: 250px;
    height: 43px;
    background: #fff;
    background-repeat: no-repeat;
    background-size: contain;
    background-color: #fff;
    margin-bottom: 25px;
    border-top-left-radius: 25px;
    border-bottom-left-radius: 25px;
    margin-left: 20px;
}

.select img{
    width: 45px;
   border: 2px solid #147BB8;
   border-radius: 50%;
   height: 100%;
}

p.select-txt{
    color: #147BB8;
    display: inline-block;
    margin: auto;
}

.offer-txt{
    color: #349F98;
    font-size: 1.4em;
}

.connect-txt{
    color: #848484;
    font-size: 1.2em;
}

.options{
    color: #848484;
    display: flex;
    flex-direction: column;
    border-top: 1px solid #000;
    border-bottom: 1px solid #000;
    width: 90%;
}

.options > div{
    display: flex;
    height: 75px;
}

.icon-box{
    width: 20%;
    height: 70px;
}

.icon-txt{
    width: 80%;
    padding-left: 30px;
}

.money-bag{
    background: url('./money-bag.png') no-repeat center;
    background-size: 70px;

}

.earth{
    background: url('./earth.png') no-repeat center;
    background-size: 70px;
}
.book{
    background: url('./book.png') no-repeat center;
    background-size: 60px;
}
.paper{
    background: url('./paper.png') no-repeat center;
    background-size: 70px;
}
.phone{
    background: url('./phone.png') no-repeat center;
    background-size: 70px;
}