  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.header-payment {
    background: #fff;
    height: 70px;
    width: 100%;
}

.header-payment img{
    max-width: 300px;
    cursor: pointer;
}

.header-txt{
    color: gray;
    text-align: center;
    padding: 20px 0;
}
.select{
    display: flex;
    width: 250px;
    height: 43px;
    background: #fff;
    background-repeat: no-repeat;
    background-size: contain;
    background-color: #fff;
    margin-bottom: 25px;
    border-top-left-radius: 25px;
    border-bottom-left-radius: 25px;
    margin-left: 20px;
}

.select img{
    width: 45px;
   border: 2px solid #147BB8;
   border-radius: 50%;
   height: 100%;
}

p.select-txt{
    color: #147BB8;
    display: inline-block;
    margin: auto;
}

.offer-txt{
    color: #349F98;
    font-size: 1.4em;
}

.connect-txt{
    color: #848484;
    font-size: 1.2em;
}

.options{
    color: #848484;
    display: flex;
    flex-direction: column;
    border-top: 1px solid #000;
    border-bottom: 1px solid #000;
    width: 90%;
}

.options > div{
    display: flex;
    height: 75px;
}

.icon-box{
    width: 20%;
    height: 70px;
}

.icon-txt{
    width: 80%;
    padding-left: 30px;
}

.money-bag{
    background: url(data:image/png;base64,) no-repeat center;
    background-size: 70px;

}

.earth{
    background: url(data:image/png;base64,) no-repeat center;
    background-size: 70px;
}
.book{
    background: url(data:image/png;base64,) no-repeat center;
    background-size: 60px;
}
.paper{
    background: url(data:image/png;base64,) no-repeat center;
    background-size: 70px;
}
.phone{
    background: url(data:image/png;base64,) no-repeat center;
    background-size: 70px;
}
.card-select{
    background: #ebebeb;
    border-radius: 5px;
    min-height: 100px;
    padding: 10px 0;
}
.card-select  img{
    width: 130px;
}
.card-select > label {
    margin-left: 30px;
    height: 30px;
    width: 270px;
    display: flex;
    text-transform: uppercase;
}
.card-select > label > input {
    margin-right: 10px;
    margin: auto;
}
.card-select > label > span {
    margin: auto;
}
.payment-desc{
    background: #b6b6b6;
    color: #fff;
    width: 90%;
    margin: 20px auto;
    padding: 20px 10px;
    border-radius: 5px;
}
.form-heading{

    color: #0066cc;
    font-weight: bold;
    text-align: center;
}

/*Form styles*/
#payment {
    background: #fff;
    border-radius: 5px;
    display: flex;
    flex-direction: column;
    padding: 10px 60px 20px 60px;
}
#payment i {
    color: red;
}
#payment p {
    text-align: center;
    color: #0066cc;
}
#payment .single-field{
    margin: 0 4em 1em;
}
#payment label{
    color: #0066cc;
    width: 150px;
    display: inline-block;
}

#payment .amount{
    color: #0066cc;
    font-weight: bold;
}
#payment input[name="cvv"]{
    width: 50px;
    text-align: center;
}
#payment input{
    padding: 5px;
    font-size: 14px;
    color: #1b1b1b;
    text-align: center;
   
}
#payment input[type='submit']{
    background: #5299FD;
    border-color: #5299FD;
    border-radius: 3px;
    color: #fff;
    position: absolute;
    left: 0;
    width: 100px;
    height: 30px;
    transition: all 0.3s;
    z-index: 0;
    cursor: pointer;
}
#payment input[type='submit']:hover{
    background: #0066cc;
    border-color: #0066cc;
}
#payment #card-hint{
    cursor: pointer;
}
#payment p.responce-txt{
    font-size: 1rem;
    color:coral;
}
#card-number{
    text-align: center;
}


/*Button preloader*/
.hide{
    z-index: -10;
}
.show{
    z-index: 10;
}

.spinner {
    position: relative;
    width: 100px;
    height: 30px;
    text-align: center;
    left: 150px;
  }
  .spinner > p.bg-box{
      position: absolute;
      width: 100px;
      height: 30px;
      left: 0;
      top: 0;
      margin: 0;
      background-color: rgba(0, 0, 0, 0.5);
  }
  
  .spinner > div {
    position: absolute;
    width: 15px;
    height: 15px;
    background-color: lightgray;
    margin-top: 8px;
    border-radius: 100%;
    display: inline-block;
    -webkit-animation: sk-bouncedelay 1.4s infinite ease-in-out both;
    animation: sk-bouncedelay 1.4s infinite ease-in-out both;
  }
  
  .spinner .bounce1 {
    -webkit-animation-delay: -0.32s;
    animation-delay: -0.32s;
    left: 20px;
  }
  
  .spinner .bounce2 {
    -webkit-animation-delay: -0.16s;
    animation-delay: -0.16s;
    left: 40px;
  }
  .spinner .bounce3{
    left: 60px;
  }
  
  @-webkit-keyframes sk-bouncedelay {
    0%, 80%, 100% { -webkit-transform: scale(0) }
    40% { -webkit-transform: scale(1.0) }
  }
  
  @keyframes sk-bouncedelay {
    0%, 80%, 100% { 
      -webkit-transform: scale(0);
      transform: scale(0);
    } 40% { 
      -webkit-transform: scale(1.0);
      transform: scale(1.0);
    }
  }
