body{
    background: #F0F0F0;   
}
section{
    width: 50%;
}
.wrapper{
    max-width: 1170px;
    margin: 0 auto;
    display: flex;
    flex-wrap: wrap;
}


/*Media for mobile devices*/
@media only screen and (max-width: 1190px) {
    #payment{
        padding: 10px 20px 20px 20px;
    }
    #payment .single-field{
        margin: 0 1em 1em;
    }
}

@media only screen and (max-width: 800px) {
    .wrapper{
        padding: 5px;
        
    }
    .header-txt{
        padding: 0;
    }
    .connect-txt, .offer-txt{
        text-align: center;
    }
    #payment{
        padding: 10px 20px 20px 20px;
       
    }
    #payment .single-field{
        margin: 0 1em 1em;
    }
    #payment input{
        text-align: center;
        margin-top: 5px;
        font-size: 1.2em;
        width: 95%;
    }
    #payment label{
        width: 110px;
        margin-right: 5px;
    }
    section{
        width: 100%;
    }
    section:nth-child(1){ /*Choose section*/
        order: 0;
    }
    section:nth-child(2){  /*Payment section*/
        order: 1;
    }
    section:nth-child(3){ /*Security section*/
        order: 3;
    }
    section:nth-child(4){ /*Terms section*/
        order: 2;
    }
    .options > div{
        height: auto;
    }
    .icon-box{
        width: 25%;
    }
    .select{
        width: auto;
        margin: 20px 0 0 0;
    }
    .options{
        width: 100%;
    }
    .icon-txt{
        padding-left: 15px;
    }

    #payment input[name="cvv"]{
        display: inline-block;
    }
    #payment input[type='submit']{
        height: 45px;
        width: 100%;
        margin: 0;
    }
    .spinner{
        width: 100%;
        left: 0;
    }
    .spinner > p.bg-box{
        width: 100%;
        height: 45px;
    }
    .spinner > div{
        margin-top: 15px;
    }
    .spinner .bounce1{
        left: 35%;
    }
    .spinner .bounce2{
        left: 45%;
    }
    .spinner .bounce3{
        left: 55%;
    }

    .rc-tooltip-arrow{
        display: none;
    }

}
