.card-select{
    background: #ebebeb;
    border-radius: 5px;
    min-height: 100px;
    padding: 10px 0;
}
.card-select  img{
    width: 130px;
}
.card-select > label {
    margin-left: 30px;
    height: 30px;
    width: 270px;
    display: flex;
    text-transform: uppercase;
}
.card-select > label > input {
    margin-right: 10px;
    margin: auto;
}
.card-select > label > span {
    margin: auto;
}
.payment-desc{
    background: #b6b6b6;
    color: #fff;
    width: 90%;
    margin: 20px auto;
    padding: 20px 10px;
    border-radius: 5px;
}
.form-heading{

    color: #0066cc;
    font-weight: bold;
    text-align: center;
}

/*Form styles*/
#payment {
    background: #fff;
    border-radius: 5px;
    display: flex;
    flex-direction: column;
    padding: 10px 60px 20px 60px;
}
#payment i {
    color: red;
}
#payment p {
    text-align: center;
    color: #0066cc;
}
#payment .single-field{
    margin: 0 4em 1em;
}
#payment label{
    color: #0066cc;
    width: 150px;
    display: inline-block;
}

#payment .amount{
    color: #0066cc;
    font-weight: bold;
}
#payment input[name="cvv"]{
    width: 50px;
    text-align: center;
}
#payment input{
    padding: 5px;
    font-size: 14px;
    color: #1b1b1b;
    text-align: center;
   
}
#payment input[type='submit']{
    background: #5299FD;
    border-color: #5299FD;
    border-radius: 3px;
    color: #fff;
    position: absolute;
    left: 0;
    width: 100px;
    height: 30px;
    transition: all 0.3s;
    z-index: 0;
    cursor: pointer;
}
#payment input[type='submit']:hover{
    background: #0066cc;
    border-color: #0066cc;
}
#payment #card-hint{
    cursor: pointer;
}
#payment p.responce-txt{
    font-size: 1rem;
    color:coral;
}
#card-number{
    text-align: center;
}


/*Button preloader*/
.hide{
    z-index: -10;
}
.show{
    z-index: 10;
}

.spinner {
    position: relative;
    width: 100px;
    height: 30px;
    text-align: center;
    left: 150px;
  }
  .spinner > p.bg-box{
      position: absolute;
      width: 100px;
      height: 30px;
      left: 0;
      top: 0;
      margin: 0;
      background-color: rgba(0, 0, 0, 0.5);
  }
  
  .spinner > div {
    position: absolute;
    width: 15px;
    height: 15px;
    background-color: lightgray;
    margin-top: 8px;
    border-radius: 100%;
    display: inline-block;
    -webkit-animation: sk-bouncedelay 1.4s infinite ease-in-out both;
    animation: sk-bouncedelay 1.4s infinite ease-in-out both;
  }
  
  .spinner .bounce1 {
    -webkit-animation-delay: -0.32s;
    animation-delay: -0.32s;
    left: 20px;
  }
  
  .spinner .bounce2 {
    -webkit-animation-delay: -0.16s;
    animation-delay: -0.16s;
    left: 40px;
  }
  .spinner .bounce3{
    left: 60px;
  }
  
  @-webkit-keyframes sk-bouncedelay {
    0%, 80%, 100% { -webkit-transform: scale(0) }
    40% { -webkit-transform: scale(1.0) }
  }
  
  @keyframes sk-bouncedelay {
    0%, 80%, 100% { 
      -webkit-transform: scale(0);
      transform: scale(0);
    } 40% { 
      -webkit-transform: scale(1.0);
      transform: scale(1.0);
    }
  }