.security-block{
    display: flex;
    width: 100%;
    background: #fff;
}
.security-img{
    height: 80px;
}
.msc{
    background: url('./credits.png');
    background-size:100%;
    background-position: center;
    background-repeat: no-repeat;
    width: 40%;
}
.ssl{
    background: url('./ssl-image.png');
    background-repeat: no-repeat;
    background-size:55%;
    background-position: center;
    width: 10%;
}
.pci{
    background: url('./pci.png');
    background-repeat: no-repeat;
    background-size:100%;
    background-position: center;
    width: 25%;
}
.comodo{
    background: url('./comodo.png');
    background-repeat: no-repeat;
    background-size:90%;
    background-position: center;
    width: 25%;
}